import React, { Component } from 'react'
import styled, {keyframes} from 'styled-components'

const warning = keyframes`
  0% {color: #beb4ab; transform: scale(1, 1);}
  50% {color: #a86e40; transform: scale(1.125, 1.125);}
  100% {color: #beb4ab; transform: scale(1, 1);}
`
const TimerDiv = styled.div`
  align-content: center;
  display: grid;
  justify-content: center;
  height: 100%;
  width: 100%;
`
const Heading = styled.div`
  align-items: center;
  border-bottom: 1px solid rgba(185, 177, 148, 0.34);
  display: flex;
  justify-content: space-around;
  > h1 {
  color: #9a8f65;
  font-weight: 100;
  }
`
const P = styled.p`
  color: #beb4ab;
  font-size: .66rem;
  font-weight: 200;
  margin: 0;
  opacity: .66;
  text-align: center;
`
const TimeDiv = styled.div`
  align-items: center;
  display: flex; 
  justify-content: center;
  > div {
    min-height: 1.125em;
    padding: .25rem 0;
    > h2 {
      color: #beb4ab;
      font-size: 2.75rem;
      font-weight: 200;
      margin: 0 0 .125rem;
      text-align: center;
    }
  }
`
const ChoicesDiv = styled.div`
  border-bottom: 1px solid rgba(185, 177, 148, 0.34);
  border-top: 1px solid rgba(185, 177, 148, 0.34);
  display: flex;
  justify-content: center;
  height: 100%;
  padding: .25rem 0;
  text-align: center;
  > div {
    align-content: space-between;
    display: grid;
    justify-content: space-between;
    justify-items: center;
    > svg {
      align-self: center;
      height: 1.5rem;
      width: 1.5rem;
    }
    > h5, div h5 {
      color: #beb4ab;
      font-size: .75rem;
      font-weight: 100;
      margin: .25rem 0;
    }
  }
`
const Button = styled.button`
  background-color: rgba(255,255,255,.085);
  border: none;
  border-radius: 3px;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  color: #beb4ab;
  font-size: 1rem;
  font-family: "Trebuchet MS", Helvetica, sans-serif;
  margin: .5rem;
  padding: .25rem .5rem;
  transition: box-shadow, transform 860ms cubic-bezier(0.25, 0.8, 0.25, 1);
  &:hover {
    box-shadow: rgba(0, 0, 0, 0.12) 0px 4px 12px, rgba(0, 0, 0, 0.12) 0px 6px 6px;
    color: #c0c0c0;
    transform: scale(1.125, 1.125);
  
  &:nth-of-type(1){
    background-color: #6b8251;
  }
  &:nth-of-type(2){
    background-color: #9a8f65;
  }
  &:nth-of-type(3){
    background-color: #a86e40;
  }
  }
`
const Input = styled.input`
  background-color: #342f28;
  border: 2px solid #9a8f65;
  border-radius: 4px;
  color:  #b3b3b3;
  font-size: .7rem;
  height: 1.4rem;
  text-align: center;
  width: 2.25rem;
`
const H3 = styled.h3 `
  align-items: center;
  animation: ${props => props.warn === 'warns' ? `${warning} 1.6s ease-in-out 6 forwards` : 'none'};
  color: #9a8f65;
  display: flex;
  font-weight: 100;
  justify-content: center;
  min-height: 2rem;
  text-align: center;
`

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      warn: 'warn',
      done: '',
      isrunning: false,
      selected: false,
      counter: 0,
      count: 0,
      change: '',
      changev: '',
      clock: '00:00',
      time: 0
    }

    this.reset = this.reset.bind(this)
    this.start = this.start.bind(this)
    this.pause = this.pause.bind(this)
    this.set = this.set.bind(this)
    this.handleChange = this.handleChange.bind(this)
    this.handleKeyPress = this.handleKeyPress.bind(this)
   }

 setTime(time) {
    if (time >= 3600) {
   let hours = Math.floor(time / 3600)
   let minutes = Math.floor((time - (hours * 3600)) / 60)
   let seconds = time % 60
   seconds = seconds.toString().length === 1 ? '0' + seconds : seconds
   minutes = minutes.toString().length === 1 ? '0' + minutes : minutes
   hours = hours.toString().length === 1 ? '0' + hours : hours
      this.setState({
        clock: `${hours}:${minutes}:${seconds}`
      })
   } else {
    let minutes = Math.floor(time / 60)
    let seconds = time % 60
        seconds = seconds.toString().length === 1 ? '0' + seconds : seconds
        minutes = minutes.toString().length === 1 ? '0' + minutes : minutes
       this.setState({
        clock: `${minutes}:${seconds}`
      })
    }
 }
  tick() {
    this.setState(prevState => ({
      counter: prevState.counter - 1
    }))
    this.setTime(this.state.counter)
    if (this.state.counter === 0) {
    this.setState({
      count: 0,
      counter: 0,
      isrunning: false,
      warn: 'warns'
      })
     this.soundInterval = setInterval(() => this.sound(), 266)
     this.soundTimeout = setTimeout(() => this.soundDone(), 4800)
     this.reset()
     this.setTime(this.state.counter)
    }
  }
  
  soundDone() {
    clearInterval(this.soundInterval)
    clearTimeout(this.soundtimeout)
    this.setState({
      done: 'Timer ended!'
    })
  }
  
  setdone() {
    this.setState({
      warn: 'warn'
    })
  }

  handleChange(ev) {
    let val = ev.target.value.substr(0,3)
    if (val > 479) {
      this.setState({
        change: 480,
        changev: 480 * 60,
        done: '8 hours is maximum!'
      })
    } 
    if (val <= 479) {
    this.setState({
      change: val,
      changev: val * 60,
      done: ''
    })
   }
    if (val < 1) {
      this.setState({
      change: '',
      changev: '',
        done: 'Please try again!'
    })
    }
  }
  
  handleKeyPress(ev) {
    ev.stopPropagation()
    if (ev.key === 's') {
       this.start()
     } 
    if (ev.key === 'p') {
       this.pause()
     }
    if (ev.key === 'r') {
      this.reset()
     } else {
      return null
    }
  }

  set(ev) {
    if (!this.state.selected) {
      let values = ev.target.value
      values = Number(values)
      this.setTime(values)
      this.setState({
       done: '',
       counter: values,
       count: values,
       selected: true
     })
    if (values < 1 || values === isNaN) {
      this.setState({
        change: '',
        changev: '',
        done: 'Set at least 1 min!',
        selected: false
      })
    }
    } else { return null }
   }
  
  start() {
    if (this.state.counter === 0) {
    clearInterval(this.interval)
    this.timeout = setTimeout(() => this.setdone(), 4200)
      this.setState({
        isrunning: false,
        selected: false,
        done: 'Please select a time!'
      })
    } 
    if (this.state.selected) {
    clearTimeout(this.timeout)
    if (!this.state.isrunning) {
    this.interval = setInterval(() => this.tick(), 1000)
     this.setState({
      done: '',
      isrunning: true,
      selected: true
     })
    }
   }
  }
  
  pause() {
    if (this.state.isrunning) {
    clearInterval(this.interval)
    this.setState(prevState => ({
      clock: prevState.clock,
      isrunning: false,
      done: 'Timer Paused'
      }))
    }
  }

  reset() {
    this.setTime(0)
    clearInterval(this.interval)
    this.timeout = setTimeout(() => this.setdone(), 4600)
    this.setState({
      count: 0,
      counter: 0,
      isrunning: false,
      change: '',
      changev: '',
      done: '',
      selected: false
    })
  }
  
  
 sound() {
   this.setState({
     done:  'Time completed!'
   })
  let asterisk = new
  Audio('data:audio/wav;base64,')
  asterisk.play()
}

  render() {
      return (
      <TimerDiv id="timer-div" onKeyPress={this.handleKeyPress}>
        <Heading>
        <svg xmlns="http://www.w3.org/2000/svg" width="18.261" height="26" viewBox="0 0 18.26146 25.999942"><path fill="none" stroke="#9a8f65" strokeWidth="1.11" d="M1.35 22.77c-.44-.57-.18-1.92.12-2.8.93-2.77 2.47-5.48 3.93-8.1 2-3.62 6.68-9.83 5.08-11.04C8.88-.4 2.73 2.9 1.06 6.03c-.5.93-1 2.3.5 3.25C4.5 10.86 7 11.93 9.18 13c2.15 1.1 4.53 2.14 7.52 3.7 1.48.95 1 2.33.48 3.27-1.68 3.13-7.82 6.43-9.43 5.2-1.6-1.2 3.06-7.42 5.08-11.05 1.46-2.6 3-5.32 3.94-8.08.3-.9.54-2.25.1-2.8" strokeLinecap="round" strokeLinejoin="round"/></svg>
          <h1>Work Timer</h1>
        </Heading>
        <TimeDiv>
          <div>
            <h2>{this.state.clock}</h2>
          </div>
        </TimeDiv>
        <TimeDiv>
          <Button onClick={this.start}>START</Button>
          <Button onClick={this.pause}>PAUSE</Button>
          <Button onClick={this.reset}>RESET</Button>
        </TimeDiv>
        <H3 warn={this.state.warn}>{this.state.done}</H3>
        <ChoicesDiv id="choices-div">
          <div>
            <Button value="300" onClick={this.set}>SHORT</Button>
            <svg xmlns="http://www.w3.org/2000/svg" width="45" height="45" viewBox="0 0 11.90625 11.90625"><path fill="#9e926a" d="M5.95 0A5.95 5.95 0 0 0 0 5.95a5.95 5.95 0 0 0 5.95 5.96 5.95 5.95 0 0 0 5.96-5.95A5.95 5.95 0 0 0 5.96 0zm0 .93a5.03 5.03 0 0 1 5.03 5.02 5.03 5.03 0 0 1-5.03 5.03A5.03 5.03 0 0 1 .93 5.95 5.03 5.03 0 0 1 5.95.93zm0 .98c-.22 0-.4.23-.4.5v2.9a.76.76 0 0 0-.35.65.76.76 0 0 0 .75.76.76.76 0 0 0 .2-.02l1.7 1.68c.15.16.4.16.56 0 .17-.15.17-.4 0-.56L6.7 6.08a.76.76 0 0 0 0-.13.76.76 0 0 0-.35-.64V2.4c0-.27-.17-.5-.4-.5z"></path></svg>
            <h5>5 Minutes</h5>
          </div>
          <div>
            <Button value="1500" onClick={this.set}>LONG</Button>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 11.90625 11.90625"><path fill="#9e926a" d="M5.95 0A5.95 5.95 0 0 0 0 5.95a5.95 5.95 0 0 0 5.95 5.96 5.95 5.95 0 0 0 5.96-5.95A5.95 5.95 0 0 0 5.96 0zm0 .93a5.03 5.03 0 0 1 5.03 5.02 5.03 5.03 0 0 1-5.03 5.03A5.03 5.03 0 0 1 .93 5.95 5.03 5.03 0 0 1 5.95.93zm0 .98c-.22 0-.4.23-.4.5v2.9a.76.76 0 0 0-.35.65.76.76 0 0 0 .75.76.76.76 0 0 0 .2-.02l1.7 1.68c.15.16.4.16.56 0 .17-.15.17-.4 0-.56L6.7 6.08a.76.76 0 0 0 0-.13.76.76 0 0 0-.35-.64V2.4c0-.27-.17-.5-.4-.5z"/></svg>
            <h5>25 Minutes</h5>
          </div>
          <div>
            <Button id="setval" value={this.state.changev} onClick={this.set}>_ SET</Button>
            <div className="choice">
           <Input id="time" value={this.state.change} onChange={this.handleChange}></Input>
            <h5>Minutes</h5>
            </div>
          </div>
         </ChoicesDiv>
          <P>Keyboard: s - Start, p - Pause, r - Reset</P>
      </TimerDiv>
    )
  }
}

export default App



